import service from "../../service.js";
import headers from "../../components/header_nav.vue"
export default {
	components: {
		headers
	},
	data() {
		return {
			Htitle: "填写地址",
			Hlicon: "../../static/icon/left_whitch.png",
			pos_id:0,
			addList:[],
			index:0,
			// hstyle:"background:transparent"
		}
	},
	onLoad:function(e){
		this.pos_id = e.id;
		console.log(e)
		service.auth(this , service.api.pos.fields , 'GET' , {
			// status:status,
			type:3,
			token:uni.getStorageSync('token')
		} , function(self,res){
			console.log(res)
			self.addList = res.data
		})
	},
	methods:{
		input(e){
			this.addList[this.index].default_str = e.detail.value;
		},
		blur(index){
			this.index = index;
		},
		submit(){
			var self = this;
			var text = '';
			var arr = [];
			var list = this.addList;
			for(var i = 0;i < list.length;i++){
				if(list[i].must == 1){
					if(list[i].default_str == ''){
						uni.showToast({
							icon: 'none',
							title: list[i].tips + '不能为空'
						});
						return;
					}else{
						arr.push(list[i].default_str);
					}
				}
			}
			service.downLoad(this,service.api.pos.check,{
				'pos_id':this.pos_id,
				'pos_fields':arr
			},'POST', function(self, res) {
				uni.showToast({
					icon: 'none',
					title: res.info
				});
				if(res.code == 1){
					setTimeout(function(){
						uni.navigateTo({
							url:"/pages/service/post"
						})
					},1500)
				}
			});
		}
	}
}
